import React from 'react';
import s from './RightMessage.module.css';
import { Row, Col, Container } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';

const RightMessage = (props) =>{
    let path = "/dialogs/" + props.id;

    return( 
    <div>
        <NavLink to={path}>
        <div className={s.rightmsg}>
            <div className={s.messageContent}>
            <div className={s.messageRadius}>
                <img src="https://i.pinimg.com/originals/bb/e2/1f/bbe21faf9642c04f811f2f87893fc174.jpg" alt="user photo"/>
            </div>
            <div className={s.messageHeading}>
                <span>{props.name}</span>
                <span>{props.message}</span>
            </div>
            </div>
            <div className={s.messageDate}>
                <span>{props.date}</span>
                <span>{props.time}</span>
            </div>
        </div>
        </NavLink>
    </div>    
    )
}

export default RightMessage;