import React from 'react';
import s from './ConversationBox.module.css';
import ConversationHeader from './ConversationHeader/ConversationHeader';
import {Redirect} from 'react-router-dom';
import {reduxForm, Field} from 'redux-form';
import {Textarea} from '../../common/FormsControls/FormsControls';
import {required, maxLengthCreator} from './../../../utils/validators/validators';


let maxLength100 = maxLengthCreator(100);

const Message = (props) =>{
    return(
        <div className={s.messageBox}>
            <div className={s.imgWrapper}>
            <img src="https://i.pinimg.com/originals/bb/e2/1f/bbe21faf9642c04f811f2f87893fc174.jpg" alt="user photo" />
            </div>
            <div className={s.message}><p>{props.message}</p></div>
        </div>
    )
}

const MessageForm = (props) =>{
    return(
        <form className={s.messageField} onSubmit={props.handleSubmit}>
                    <Field type={"text"} name={"message"} placeholder={"Type a message here"} component={Textarea} 
                    validate={[required, maxLength100]}/>
					<button type="submit">Send</button>
				</form>
    )
}


const ConversationBox = (props) =>{    

let messages = props.dialogsPage.messages
                        .map( m => <Message message={m.message} key={m.id} id={m.id}/>)                    
                        
let addNewMessage = (values) => {
    props.addMessage(values.message) 
}    

if(!props.isAuth) return <Redirect to={'/login'}/>

    return(
        <div className={s.conversationWrapper}>
            <ConversationHeader name="Sunny Day" status="online"/>
            <div className={s.messageBoxWrapper}>
                {messages}
            </div>
            <div className={s.messageSendArea}>
            <MessageReduxForm onSubmit={addNewMessage}/>
		</div>
        </div>
    )
}

const MessageReduxForm = reduxForm({form: 'dialogAddMessageForm'}) (MessageForm)

export default ConversationBox;