import React from 'react';
import s from './Rightbox.module.css';
import RightMessage from './RightMessage/RightMessage';

const Rightbox = (props) =>{
  

    let dialogsElements = 
    props.dialogs.map(d => <RightMessage id={d.id} name={d.name} key={d.id} message={d.message} date={d.date} time={d.time}/>
    )


    return(
        <div className={s.rightBox}>
            { dialogsElements}
        </div>
    )
}

export default Rightbox;